/* Global styles */
body {
  font-family: 'Roboto', sans-serif;
  background-color: #f7fafc;
  color: #333;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: linear-gradient(to right, #e3f2fd, #bbdefb);
}

/* Container for the game */
.w-full {
  width: 100%;
}
.max-w-md {
  max-width: 510px;
}
.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

/* Timer and score styles */
.mb-4 {
  margin-bottom: 1.5rem;
}
.text-xl {
  font-size: 1.25rem;
  font-weight: 600;
  color: #333;
}
.font-bold {
  font-weight: 700;
  padding: 1.2vh;
}
.flex {
  display: flex;
}
.justify-between {
  justify-content: space-between;
}
.items-center {
  align-items: center;
}

/* Card grid layout */
.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  padding: 1.5rem;
  background-color: #f1f5f9;
  border-radius: 1rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

/* Card styles */
.w-24 {
  width: 6rem;
}
.h-32 {
  height: 8rem;
}
.cursor-pointer {
  cursor: pointer;
}
.rounded-lg {
  border-radius: 1rem;
}
.text-3xl {
  font-size: 2.5rem;
  transition: transform 0.3s ease;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.items-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}

/* Card flipping animations */
.bg-game-background {
  background-color: #dbdbdb;
  transition: background-color 0.3s ease;
}
.bg-game-accent {
  background-color: #e0e0e0;
  transition: background-color 0.3s ease;
}
.bg-game-success {
  background-color: #22c55e;
  transition: background-color 0.3s ease;
}

/* Hover and animations for cards */
.card-hover:hover {
  transform: scale(1.1);
  box-shadow: 0 10px 20px rgba(240, 240, 240, 0.1);
}
.animate {
  animation: flipCard 0.5s forwards;
}

@keyframes flipCard {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(180deg);
  }
}

/* Button styles */
.bg-game-accent {
  background-color: #cccccc;
}
.bg-opacity-80 {
  background-color: #cccccc;
}
.hover\:bg-opacity-80:hover {
  background-color: #cccccc;
}
.text-white {
  color: white;
}
.py-2 {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}
.rounded-lg {
  border-radius: 0.5rem;
  
}
.flex {
  display: flex;
}
.items-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}
.gap-2 {
  gap: 0.5rem;
}

/* Spinning loader */
.w-4 {
  width: 1rem;
}
.h-4 {
  height: 1rem;
}
.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Responsive design for smaller screens */
@media (max-width: 500px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .w-24 {
    width: 5rem;
  }
  .h-32 {
    height: 7rem;
  }
}

